html{
  font-family: Helvetica, sans-serif;
}

.bodyback{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 9999;
    background: url(/assets/backimg.png) no-repeat center center fixed;
    background-size: cover;
    background-color:transparent;
}
.back{
    position: absolute;
    top: 30%;
    width: 100%;
    background-color: transparent;
}
.logo {
    margin-left: 1%;
    position: absolute;
    height:15%;
    width:10%;
}
.logoimgLogin{
  background-color: transparent;
  height:100%;
}
.container{
  width:100%;
}

.login-wraper{
    background-image:url("/assets/indexBackground.png");
    background-repeat: no-repeat;
}

.login-wraper .radioOne{
    font-size: 2.5em;
    margin-bottom: 20px;
}


@media only screen and (max-width: 1170px) {
   
    
    .signin-panel{
        float: left;
        width:50%;
    }
    .signup-panel{
        float: left;
        width:50%;
    }
    
}

@media only screen and (max-width: 600px) {
    .signin-panel{
        float: left;
        width:100%;
    }
    .signup-panel{
        float: left;
        width:100%;
    }
}


